@import "https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap";
* {
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
}

a {
  color: unset;
  cursor: pointer;
  padding: .5rem;
  text-decoration: none;
}

button {
  cursor: pointer;
  color: #183440;
  background: #f0f7f9;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
}

input, textarea {
  color: #f0f7f9;
  background: #183440;
  border: none;
  outline: none;
  padding: 3px 8px;
  font-size: 14px;
  font-weight: 300;
}

input::placeholder, textarea::placeholder {
  color: #f0f7f9;
  opacity: 1;
}

textarea {
  min-height: 100px;
  padding: 6px 8px;
}

body {
  color: #f0f7f9;
  background: #183440;
}

img {
  width: 100%;
}

.page-container {
  min-height: 100vh;
  position: relative;
}

.content-wrap {
  padding-bottom: 2.5rem;
}

.full-screen-message {
  height: calc(100vh - 103px);
  justify-content: center;
  align-items: center;
  display: none;
}

.full-screen-message h1 {
  text-align: center;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: 2s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.button-1 {
  width: 104px;
  height: 40px;
}

.input-div {
  flex-direction: column;
  margin-bottom: 1rem;
  display: flex;
}

.input-div label {
  margin-bottom: 8px;
  margin-right: auto;
  font-size: 14px;
  font-weight: 300;
}

.input-div input {
  line-height: 2rem;
}

.input-div button {
  width: 100%;
  margin-top: 1rem;
}

.products-section {
  padding: 0 1rem;
}

.products-container {
  max-width: 992px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: center;
  gap: 1rem;
  margin: 0 auto;
  display: grid;
}

.products-content {
  cursor: pointer;
  box-shadow: -3px 3px #05303ea8, -5px 5px #103c51;
}

.products-content .price-tag {
  color: #9db7c4;
  margin-left: 4px;
  font-size: 12px;
}

.products-content p {
  text-align: center;
  background: #0a4253;
  line-height: 1.5rem;
  transition: all .2s ease-out;
}

.products-content:hover > p {
  background: #0d546a;
}

.products-content .products-img {
  height: 200px;
  background: #0e1d24 center / cover no-repeat;
}

.mb-4 {
  margin-bottom: 4rem;
}

.footer {
  width: 100%;
  height: 2.5rem;
  background: #0e1d24;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 0;
}

.footer span {
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  font-size: 14px;
  display: flex;
}

